import { config } from './slider.js';

document.querySelector('h4').textContent = bro(`Skills`);

new Glide('.glide', config).mount();

function startTime() {
  const today = new Date();
  let h = today.getHours();
  let m = today.getMinutes();
  let s = today.getSeconds();
  h = checkTime(h);
  m = checkTime(m);
  s = checkTime(s);
  document.getElementById('clock').innerHTML = `${h}<br />${m}<br />${s}`;
  setTimeout(startTime, 500);
}
function checkTime(i) {
  if (i < 10) {
    i = `0${i}`;
  } // add zero in front of numbers < 10
  return i;
}

document.getElementById('clock').addEventListener('DOMContentLoaded', startTime());

function date() {
  const event = new Date();

  const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
  const test23 = event.toLocaleDateString('ko-KR', options);
  // const test23 = event.toLocaleDateString('de-DE', options);

  document.getElementById('datum').innerHTML = test23;
}

document.getElementById('datum').addEventListener('load', date());

const myDate = new Date();
const hrs = myDate.getHours();

let greet;

if (hrs < 12) greet = 'Guten Morgen';
else if (hrs >= 12 && hrs <= 17) greet = 'Guten Tag';
else if (hrs >= 17 && hrs <= 24) greet = 'Guten Abend';

document.getElementById('lblGreetings').innerHTML = `<b>${greet}`;

gsap.set('.wrapper1', { xPercent: -50, yPercent: -50 });
gsap.set('#no02', { x: 500 });
gsap.set('#no03', { y: 0 });

const boxWidth = 400;
const totalWidth = boxWidth * 7; //  * n of boxes
const no01 = document.querySelectorAll('#no01 .boxT');
const no02 = document.querySelectorAll('#no02 .boxT');
const no03 = document.querySelectorAll('#no03 .boxT');
const dirFromLeft = `+=${totalWidth}`;
const dirFromRight = `-=${totalWidth}`;

const mod = gsap.utils.wrap(0, totalWidth);

function marquee(which, time, direction) {
  gsap.set(which, {
    x(i) {
      return i * boxWidth;
    },
  });
  const action = gsap.timeline().to(which, {
    x: direction,
    modifiers: {
      x: x => `${mod(parseFloat(x))}px`,
    },
    duration: time,
    ease: 'none',
    repeat: -1,
  });
  return action;
}

const master = gsap
  .timeline()
  .add(marquee(no01, 20, dirFromLeft), 1)
  .add(marquee(no02, 30, dirFromLeft), 2)
  .add(marquee(no03, 30, dirFromRight), 3);

