gsap.utils.toArray('.demo-text').forEach((section, index) => {
  const w = section.querySelector('.wrapper');

  const [x, xEnd] = index % 2 ? ['100%', (w.scrollWidth - section.offsetWidth) * -1] : [w.scrollWidth * -1, 0];
  gsap.fromTo(
    w,
    { x },
    {
      x: xEnd,
      scrollTrigger: {
        start: 'top 80%',
        trigger: section,
        scrub: 0.5,
        // markers: true,
      },
    }
  );
});

