gsap.utils.toArray('.about2').forEach(section => {
  const elems = section.querySelectorAll('.best');

  // Set things up
  gsap.set(elems, { y: 50, opacity: 0 });

  ScrollTrigger.create({
    trigger: section,
    start: 'top +=50',
    end: '+=60%',
    pin: true,
    onEnter: () =>
      gsap.to(elems, {
        y: 0,
        opacity: 1,
        duration: 0.5,
        stagger: 0.1,
        delay: 0.2,
        ease: 'back.inOut(1.7)',
        overwrite: 'auto',
      }),
    onLeaveBack: () =>
      gsap.to(elems, {
        y: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1,
        delay: 0.2,
        ease: 'back.inOut(1.7)',
        overwrite: 'auto',
      }),
  });
});
