import img0 from '../images/portfolio/callme0.png';
import img1 from '../images/portfolio/callme1.png';
import img2 from '../images/portfolio/callme2.png';
// import img3 from '../images/portfolio/callme3.png';
import img4 from '../images/portfolio/callme4.png';
// import img5 from '../images/portfolio/callme5.png';
import img6 from '../images/portfolio/callme6.png';
import img7 from '../images/portfolio/callme7.png';
// import img8 from '../images/portfolio/callme8.png';
// import img9 from '../images/portfolio/callme9.png';
import img11 from '../images/portfolio/callme11.png';
import imgBg from '../images/svg/bg_purple2.svg';

function startTimer(duration, display) {
  const start = Date.now();
  let diff;
  let minutes;
  let seconds;
  function timer() {
    // get the number of seconds that have elapsed since
    // startTimer() was called
    diff = duration - (((Date.now() - start) / 1000) | 0);

    // does the same job as parseInt truncates the float
    minutes = (diff / 60) | 0;
    seconds = diff % 60 | 0;

    minutes = minutes < 10 ? `0${minutes}` : minutes;
    seconds = seconds < 10 ? `0${seconds}` : seconds;

    display.textContent = `${minutes}:${seconds}`;

    if (diff <= 0) {
      // add one second so that the count down starts at the full duration
      // example 05:00 not 04:59

      document.querySelector('#time').innerText = 'Oh Yeah!';
      //   start = Date.now() + 1000;

      const tel = document.querySelectorAll('.portfolio .portfolio-item');

      tel.forEach(tele => {
        tele.style.flexBasis = '25%';
      });

      document.querySelector('#pic1').src = img0;
      document.querySelector('#pic2').src = img1;
      document.querySelector('#pic3').src = img7;
      document.querySelector('#pic4').src = img2;
      document.querySelector('#pic5').src = img11;
      document.querySelector('#pic6').src = img2;
      document.querySelector('#pic7').src = img1;
      document.querySelector('#pic8').src = img0;
      document.querySelector('#pic9').src = img7;
      document.querySelector('#pic10').src = img4;
      document.querySelector('#pic11').src = img6;
      document.querySelector('#pic12').src = img2;

      const timeIsUp = document.querySelectorAll('.test7, test6');
      const timeIsUp1 = document.querySelectorAll('h2, h4, .marquee span');
      document.getElementById('hi').innerText = 'Hi!';
      document.getElementById('ruf').innerText = 'Ruf';
      document.getElementById('mich').innerText = 'mich';
      document.getElementById('an').innerText = 'an 🚀!';
      document.getElementById('tel').innerText = '0172/541541';

      // document.getElementById('text1').innerText = 'Test';

      document.getElementById('tooltip').setAttribute('data-tooltip', 'HI#RUF#MICH#AN');
      const urlString = `url(${imgBg})`;

      document.querySelector('.purple').style.backgroundImage = urlString;
      const telNum = document.querySelectorAll('h5');

      telNum.forEach(tele => {
        tele.innerText = '0172/54154154';
      });

      timeIsUp.forEach(preview => {
        preview.innerText = '#Oh#YEAH#HI#RUF#MICH#AN';
      });
      timeIsUp1.forEach(preview => {
        preview.innerText = 'HI! RUF MICH AN!';
      });
      document.title = 'HI#RUF#MICH#AN';
    }
  }
  // we don't want to wait a full second before the timer starts
  timer();
  setInterval(timer, 1000);
}

window.onload = function() {
  const fiveMinutes = 60 * 5;
  const display = document.querySelector('#time');
  startTimer(fiveMinutes, display);
};
